<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
     <!--  -->
    <div v-if="!$route.meta.hideNav" class="nav-container">
      <div class="header-section">
        <h1 class="title"></h1>
        <span class="designer">{{ username }}</span>
      </div>
      <nav class="nav-menu">
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'home' }" @click.prevent="handleSelect('home')"></a>
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'interview' }" @click.prevent="handleSelect('interview')"></a>
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'personal' }" @click.prevent="handleSelect('personal')"></a>
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'friends' }" @click.prevent="handleSelect('friends')"></a>
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'about' }" @click.prevent="handleSelect('about')"></a>
        <a href="#" class="nav-item" :class="{ 'active': activeIndex === 'exit' }" @click.prevent="handleSelect('exit')"></a>
      </nav>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
import avatarComponent from './views/avatardemo.vue'


export default {
  name: 'App',
  data() {
    return {
      activeIndex: 'home',
      username: ''
    }
  },
  methods: {
    handleSelect(index) {
      this.activeIndex = index;
      console.log('', index);
      if (index === 'home') {
        this.$router.push('/home');
      } else if (index === 'interview') {
        this.$router.push('/interview');
      } else if (index === 'personal') {
        this.$router.push('/personal');
      } else if (index === 'friends') {
        this.$router.push('/friends');
      } else if (index === 'about') {
        this.$router.push('/about');
      } else if (index === 'exit') {
        localStorage.removeItem('token');
        this.$router.push('/login');
      }
    },
    fetchUsername() {
      this.username = localStorage.getItem('username') || '';
    }
    
  
  },
  mounted() {
    // 
    this.fetchUsername();
    // 
    this.routeListener = this.$router.afterEach(this.fetchUsername);
  },
  beforeDestroy() {
    // 
    if (this.routeListener) {
      this.routeListener();
    }
  },
  components: {
    avatarComponent
  }
}
</script>


